@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), local("Open Sans Regular"), url("../../fonts/OpenSans-Regular.woff2") format(woff2), url("../../fonts/OpenSans-Regular.woff") format(woff);
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), local("Open Sans Bold"), url("../../fonts/OpenSans-Bold.woff2") format(woff2), url("../../fonts/OpenSans-Bold.woff") format(woff);
}
@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local("Open Sans"), local("Open Sans Light"), url("../../fonts/OpenSans-Light.woff2") format(woff2), url("../../fonts/OpenSans-Light.woff") format(woff);
}
*, *::before, *::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
}

.page-body__header {
  position: relative;
  margin-bottom: 24px;
}

.header {
  background-color: rgba(0, 0, 0, 0.3);
}
.header--menu-opened {
  background-color: #283645;
}

.header__wrapper {
  width: 280px;
  margin: 0 auto;
  padding: 19.5px 0;
  display: flex;
}

.header--menu-opened .header__wrapper {
  padding: 23px 0;
}

.header__logo {
  margin-right: auto;
}

.header__button {
  position: relative;
  width: 50px;
  height: 24px;
  border: none;
  background-color: transparent;
}
.header__button::before {
  content: "";
  position: absolute;
  top: 11px;
  right: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 -10px 0 0 #ffffff, 0 10px 0 0 #ffffff;
}

.header__button--active::before {
  top: 10px;
  right: -3px;
  width: 29px;
  height: 3px;
  transform: rotate(45deg);
  box-shadow: none;
}
.header__button--active::after {
  content: "";
  position: absolute;
  top: 10px;
  right: -3px;
  width: 29px;
  height: 3px;
  background-color: white;
  transform: rotate(-45deg);
  box-shadow: none;
}

.nav {
  display: none;
}
.nav--opened {
  display: block;
}

.nav__list {
  position: absolute;
  top: 73px;
  right: 0;
  width: 320px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.nav__item {
  background-color: #283645;
  text-align: center;
  box-shadow: 0px -1px 0px 0px #000000, 0px 1px 0px 0px rgba(255, 255, 255, 0.1490196078) inset;
}

.nav__link {
  padding: 15.5px;
  display: block;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
}

.main-promo::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 462px;
  background: #000000 url("../../../img/decorative/city-background-mobile.jpg") 0 0px no-repeat;
  z-index: -1;
}

.main-promo__slogan {
  margin: 0;
  margin-bottom: 28px;
  padding: 0 10px;
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 300;
  line-height: 28.8px;
  color: #ffffff;
  text-align: center;
}

.main-promo__app-demo {
  min-height: 286px;
  background: url("../../../img/decorative/holding-phone-mobile.png") 0 0 no-repeat, url("../../../img/decorative/phone-screen-mobile.jpg") 104px 33px no-repeat;
}

.platforms {
  padding: 28px 20px 31px;
  background-color: #1D2631;
}

.platforms__download-button {
  margin-bottom: 32px;
  padding: 15.5px 36px;
  border: 3px solid #FFFFFF;
  border-radius: 29px;
  display: block;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
}

.platforms__list {
  margin: 0 38px;
  margin-bottom: 17px;
  padding: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  list-style-type: none;
}

.platforms__item:not(:last-child) {
  margin-right: 35px;
}

.platforms__description {
  margin: 0;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  color: #ffffff;
  text-align: center;
}

.main__information {
  position: relative;
  margin-top: 41px;
}

.information__text {
  margin: 0;
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 300;
  line-height: 28.8px;
  text-align: center;
}
.information__text:first-child {
  margin-bottom: 30px;
}
.information__text:nth-child(2) {
  margin-bottom: 112px;
}

.advantages {
  position: relative;
  margin: 0;
  padding: 0;
  padding-bottom: 318px;
  background-color: #F2F2F2;
}
.advantages::after {
  content: "";
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
  width: 157px;
  height: 277px;
  margin: 0 auto;
  background: url("../../../img/decorative/screen-friends-mobile.jpg") 8px 35px no-repeat, url("../../../img/decorative/phone-background-mobile.png") 0 0 no-repeat;
}

.advantages__termin {
  margin: 0;
  margin-bottom: 12px;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
}

.advantages__description {
  margin: 0;
  padding: 0 21px;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
}
.advantages__description:not(:last-of-type) {
  margin-bottom: 31px;
}

.main__reviews {
  margin-bottom: 555px;
}

.reviews {
  padding-top: 41px;
}

.reviews__list {
  margin: 0;
  margin-bottom: 29px;
  padding: 0;
  list-style-type: none;
}

.reviews__item {
  display: none;
}
.reviews__item--active {
  display: list-item;
}

.reviews__author {
  display: block;
  margin-bottom: 13px;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  text-align: center;
}

.reviews__text {
  margin: 0;
  padding: 0 20px;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
  color: #283645;
}

.slider {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.slider__item:not(:last-child) {
  margin-right: 13px;
}

.slider__item--active .slider__button {
  background-color: #ffffff;
  box-shadow: 0 0 0 3px #283645 inset;
}

.slider__button {
  width: 12px;
  height: 12px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background-color: rgba(40, 54, 69, 0.3);
}

.prices__list {
  margin: 0;
  padding: 0;
  display: flex;
  list-style-type: none;
}

.prices {
  position: relative;
}

.prices__wrapper {
  position: absolute;
  width: 320px;
}

.price {
  width: 280px;
  border: 2px solid #ffffff;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.price--active {
  width: 280px;
}

.price__wrapper {
  background-color: #283645;
}

.price__name {
  display: block;
  font-family: Open Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 30px;
  color: #ffffff;
  text-align: center;
}

.price__tag {
  display: block;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 300;
  line-height: 30px;
  color: #ffffff;
  text-align: center;
}

.price__characteristics {
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
}

.contacts {
  padding: 0;
  padding-bottom: 41px;
}

.contacts--red-text {
  color: #D22856;
}

.contacts__description {
  margin: 0;
  margin-bottom: 34px;
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 300;
  line-height: 28.8px;
  color: #283645;
  text-align: center;
}

.contacts__text {
  display: block;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 24.51px;
  color: #283645;
  text-align: center;
}

.contacts__phone {
  margin-bottom: 17px;
  display: block;
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 300;
  line-height: 32.68px;
  text-align: center;
  text-decoration: none;
}

.contacts__email {
  margin-bottom: 22px;
  display: block;
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 300;
  line-height: 32.68px;
  text-align: center;
  text-decoration: none;
}

.contacts__address {
  margin: 0;
  padding: 0 12px;
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 300;
  line-height: 32.68px;
  text-align: center;
  text-decoration: none;
}

.location {
  position: relative;
}
.location::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 123px;
  margin: 0 auto;
  background: url("../../../img/decorative/triangle-background.svg") 50% -96px no-repeat;
}

.location__map {
  width: 100%;
  height: 200px;
  display: block;
}

.catalog__promo {
  min-height: 171px;
  padding-top: 29px;
  margin-bottom: 44px;
}

.promo::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 237px;
  background: #000000 url("../../../img/decorative/promo-tablet.png") 50% 0px/660px 237px no-repeat;
  background-size: auto;
  z-index: -1;
}

.promo__heading {
  margin: 0;
  padding: 0 5px;
  font-family: Open Sans;
  font-size: 24px;
  font-weight: 300;
  line-height: 32.68px;
  text-align: center;
}
.promo__heading--white-text {
  color: #ffffff;
}

.catalog__photos {
  margin-bottom: 45px;
}

.photos__text {
  margin: 0;
  margin-bottom: 50px;
  padding: 0 35px;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
}

.photos__panorama {
  margin-bottom: 20px;
}

.panorama__image-wrapper {
  position: relative;
  width: 320px;
  margin: 0 auto;
}
.panorama__image-wrapper::before {
  content: "Панорама дня";
  position: absolute;
  top: 0;
  right: 20px;
  width: 140px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #D22856;
  font-family: Open Sans;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
}

.panorama__image {
  display: block;
}

.panorama__comment {
  width: 280px;
  margin: 0 auto;
  padding: 17.5px 17px;
}

.gallery__comment {
  width: 280px;
  margin: 0 auto;
  padding: 17.5px 17px;
}

.comment {
  background-color: #F2F2F2;
}

.comment__author {
  margin: 0;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
}
.comment__author--blue-text {
  color: #283645;
}

.comment__published-time {
  font-family: Open Sans;
  font-size: 12px;
  font-weight: 300;
  line-height: 24px;
  text-transform: uppercase;
}
.comment__published-time--blue-text {
  color: #283645;
}

.comment__text {
  margin: 0;
  margin-bottom: 10px;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.comment__button {
  padding-left: 32px;
  border: none;
  background: url("../../../img/decorative/heart.svg") left center no-repeat;
}

.comment__likes {
  font-family: Open Sans;
  font-size: 12px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
}
.comment__likes--red-text {
  color: #D22856;
}

.gallery {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.photos__gallery {
  width: 280px;
  margin: 0 auto;
}

.gallery__image {
  display: block;
}

.gallery__item:not(:last-child) {
  margin-bottom: 20px;
}

.upload__text {
  margin: 0;
  margin-bottom: 42px;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  text-align: center;
}
.upload__text--blue-text {
  color: #283645;
}

.upload__upload-menu {
  width: 100%;
  margin: 0 auto;
  padding: 20px 0 41px;
}

.upload-menu--grey {
  background-color: #F2F2F2;
}

.upload-menu__uploading-image {
  margin: 0 auto 41px;
  display: block;
}

.upload-menu__wrapper--mobile {
  margin-bottom: 74px;
}

.upload-menu__filters {
  margin: 0 69px 35px 57px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}

.upload-menu__filter:not(:last-child) {
  margin-right: 35px;
}

.upload-menu__filter-button {
  padding: 0;
  border: none;
}

.upload-menu__effect-range {
  width: 240px;
  height: 2px;
  margin: 0 auto;
  background-color: #283645;
  display: block;
  -webkit-appearance: none;
  appearance: none;
}

.upload-menu__effect-range::-webkit-slider-thumb {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #D22856;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
}

.upload-menu__effect-range::-moz-slider-thumb {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #ffffff;
  border: 2px solid #D22856;
  cursor: pointer;
}

.upload__button {
  width: 280px;
  min-height: 55px;
  margin: 0 auto 12px;
  padding: 0;
  border: 3px solid #D22856;
  border-radius: 30px;
  display: block;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  text-align: center;
}
.upload__button--red-text {
  color: #D22856;
}

.upload__cancel-upload {
  width: 280px;
  min-height: 55px;
  margin: 0 auto;
  padding: 0;
  border: 3px solid rgba(40, 54, 69, 0.1);
  border-radius: 30px;
  display: block;
  font-family: Open Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  text-align: center;
}
.upload__cancel-upload--light-text {
  color: rgba(40, 54, 69, 0.1);
}

.footer {
  margin-top: auto;
  padding: 40px 0;
}

.footer__logo {
  margin: 0 auto 41px;
  display: block;
  text-align: center;
}

.socials {
  margin: 0;
  margin-bottom: 41px;
  padding: 0;
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.socials__item:not(:last-child) {
  margin-right: 14px;
}

.socials__link {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  box-shadow: 0 0 0 3px rgba(40, 54, 69, 0.1);
}

.copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-decoration: none;
}

.copyright__text {
  margin-right: 15px;
  font-family: Open Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
.copyright__text--blue {
  color: #283645;
}